<template>
  <div class="login">
    <van-nav-bar>
      <template #left>
        <div class="left-nav">
          <div class="logo">
            <img
              class="auto-img"
              src="../assets/images/logo.png"
              alt=""
            />
          </div>
          <div class="logo-title">Luckin Coffee</div>
        </div>
      </template>

      <template #right>
        <div class="right-title" @click="toMain">先逛一逛</div>
      </template>
    </van-nav-bar>

    <div class="login-box">
      <div class="login-info">
        <div>
          <div class="welcome">欢迎回来！</div>
          <div class="en-welcome">Please login to your account</div>
        </div>

        <div class="login-form">
          <van-form @submit="login">
            <van-field
              v-model="loginUserInfo.phone"
              name="手机号"
              label="手机号"
              placeholder="输入手机号"
              autocomplete="off"
            />
            <van-field
              v-model="loginUserInfo.password"
              :type="loginPasswordStatus ? 'password' : 'text'"
              name="密码"
              label="密码"
              placeholder="输入密码"
              :right-icon="loginPasswordStatus ? 'closed-eye' : 'eye-o'"
              autocomplete="off"
              @click-right-icon="togglePassword('loginPasswordStatus')"
            />

            <div class="forgot">
              <div class="forgot-text" @click="toFind">忘记密码</div>
            </div>

            <div class="login-btn">
              <van-button round block color="#eb2403" native-type="submit"
                >登&nbsp;&nbsp;录</van-button
              >
            </div>

            <div class="login-btn">
              <van-button round block @click="showPopup"
                >注&nbsp;&nbsp;册</van-button
              >
            </div>
          </van-form>
        </div>
      </div>
    </div>

    <!-- 注册 -->
    <van-popup v-model="show" position="bottom" closeable>
      <div class="register">
        <div class="register-title">注册</div>
        <div class="reg-form">
          <van-form @submit="register">
            <van-field
              v-model="regUserInfo.phone"
              name="手机号"
              label="手机号"
              placeholder="输入手机号(11位)"
              autocomplete="off"
            />
            <van-field
              v-model="regUserInfo.password"
              :type="passwordStatus ? 'password' : 'text'"
              name="密码"
              label="密码"
              placeholder="输入密码(必须字母开头6-16位)"
              :right-icon="passwordStatus ? 'closed-eye' : 'eye-o'"
              autocomplete="off"
              @click-right-icon="togglePassword('passwordStatus')"
            />

            <van-field
              v-model="regUserInfo.nickName"
              type="text"
              name="昵称"
              label="昵称"
              placeholder="输入昵称(支持中英文组合1-10位)"
              autocomplete="off"
            />

            <div class="login-btn">
              <van-button round block color="#eb2403" native-type="submit"
                >注&nbsp;&nbsp;册</van-button
              >
            </div>
          </van-form>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      //是否显示弹出层
      show: false,

      //用户登录信息
      loginUserInfo: {
        phone: "",
        password: "",
      },

      //注册用户信息
      regUserInfo: {
        phone: "",
        password: "",
        nickName: "",
      },

      //是否显示注册密码
      passwordStatus: true,

      //是否显示登录密码
      loginPasswordStatus: true,
    };
  },

  methods: {
    //显示弹出层
    showPopup() {
      this.show = true;
    },
    // 跳转到主界面
    toMain(){
        this.$router.push({name: 'Menu'})
    },
    //切换注册密码状态
    togglePassword(key) {
      this[key] = !this[key];
    },

  toFind(){
      this.$router.push({name: "FindPassword"})
  },
    //注册
    register() {
      //表单验证
      //验证手机号
      let registerPhoneReg = /^1[3-9]\d{9}$/;
      if (!registerPhoneReg.test(this.regUserInfo.phone)) {
        //手机号验证不通过
        this.$toast("手机号错误");

        return;
      }

      //验证密码
      //支持字母数字组合, 且必须以字母开头, 6-16位
      let registerPasswrodReg = /^[A-Za-z][A-Za-z\d]{5,15}$/;
      if (!registerPasswrodReg.test(this.regUserInfo.password)) {
        this.$toast("密码支持字母数字组合");
        return;
      }

      //验证昵称
      //支持中英文组合, 1-10位
      let registerNickNameReg = /^[A-Za-z\u4e00-\u9fa5]{1,10}$/;
      if (!registerNickNameReg.test(this.regUserInfo.nickName)) {
        this.$toast("昵称格式错误");
        return;
      }

      

      //发起注册请求
      this.axios({
        method: "POST",
        url: "/register",
        data: this.regUserInfo
      })
        .then((res) => {
          

          this.$toast(res.data.msg);

          if (res.data.code === 100) {
            //注册成功
            this.show = false;
          }
          
          
        })
        .catch((err) => {
          
        });
    },

    //登录
    login() {
      //表单验证
      //验证手机号
      let lgoinPhoneReg = /^1[3-9]\d{9}$/;
      if (!lgoinPhoneReg.test(this.loginUserInfo.phone)) {
        //手机号验证不通过
        this.$toast("手机号错误");

        return;
      }

      //验证密码
      //支持字母数字组合, 且必须以字母开头, 6-16位
      let lgoinPasswrodReg = /^[A-Za-z][A-Za-z\d]{5,15}$/;
      if (!lgoinPasswrodReg.test(this.loginUserInfo.password)) {
        this.$toast("密码支持字母数字组合");
        return;
      }


      

      //发起注册请求
      this.axios({
        method: "POST",
        url: "/login",
        data: this.loginUserInfo
      })
        .then((res) => {
          

          this.$toast(res.data.msg);

          if (res.data.code === 200) {
            //登录成功
            //将token保存在cookie
            this.$cookies.set('TOKEN', res.data.token, '10d');
            setTimeout(() => {
              this.$router.push({name: 'Menu'});
            }, 2000)
          }
          
        })
        .catch((err) => {
          
        });
    }
  },
};
</script>

<style lang="less" scoped>
.login {
  .reg-form {
    margin-top: 30px;
  }
  .register {
    padding: 15px 10px 10px;
  }

  .register-title {
    font-size: 18px;
  }

  .forgot {
    margin-top: 20px;
    display: flex;
  }

  .forgot-text {
    margin-left: auto;
    color: #666;
    font-size: 14px;
  }

  .login-btn {
    margin-top: 50px;
  }

  .login-form {
    margin-top: 50px;
  }

  .welcome {
    font-size: 24px;
    font-weight: bold;
  }

  .en-welcome {
    font-size: 18px;
    color: #999;
    margin-top: 20px;
  }

  .left-nav {
    display: flex;
    align-items: center;
  }

  .logo {
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }

  .logo-title {
    margin-left: 10px;
    font-size: 14px;
    font-weight: bold;
  }

  .right-title {
    font-size: 14px;
    color: #eb2403;
    font-weight: bold;
  }

  .login-box {
    position: fixed;
    top: 46px;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #fff;
  }

  .login-info {
    padding: 100px 10px 10px;
  }
}
</style>